@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);

.layout__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 100%;
  background-color: rgb(75,175,75);
  max-height: 5vh;
}

.layout__logo {
  display: flex;
  justify-content: space-around;
  margin-left: 20px;
  font-size: 2rem;
  font-family: 'Open Sans', sans-serif; 
  font-weight: bolder;
}


.layout__menuitems li {
  display: inline-block;
  margin: 0px 27px 0px 27px;
  font-size: 1.2rem;
  font-family: 'Open Sans', sans-serif; 
  font-weight: bolder;  
  position: relative;
  padding-top: 25px;
  padding-bottom: 25px;
}


.layout__menuitems li .sub-menu {
  padding: 30px;
  width: 227px;
  position: absolute;
  top: 100%;
  left: 0;
  background: #fff;
  border-top: 4px solid #07a8ff;
  opacity: 0;
  pointer-events: none;
  box-shadow: 1px 8px 13px #c5c5c5;
  z-index: 7;
}
.layout__menuitems li .sub-menu li {
  margin: 0;
  padding-top: 0px;
  padding-bottom: 17px;
  display: block;
}
.layout__menuitems li:hover>.sub-menu {
  pointer-events: visible;
  opacity: 1;
}
.layout__header a{
  color: whitesmoke;
  text-decoration: none;
  display: flex;
  justify-content: space-around;
}
.layout__header a:hover{
  color: black;
}
.layout__header .is-active{
  background-color: rgb(75,175,75);
  color: rgb(255,255,255);
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 5px;
  text-decoration: underline;
  text-underline-offset: 3px;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  text-decoration-thickness: 2px;
}
.layout__page{
  width: 100%;
  position: relative;
  margin-top: 10%;
}
.layout__page h1{
  text-align: center;
  font-size: 60px;
  color: #07a8ff;
}


@media (max-width: 780px) { 
.layout__response__menubar {
    display: table!important;
    float: none;
    background-color: rgb(75,175,75);
    color:whitesmoke;
    padding-right: 20px;
    padding-top: 2px;
    margin: 10px auto;
}
.layout__hidemenu {
  display: none;
  width: 100%;
  margin: 0 auto;
  padding: 17px;
  border-bottom: 5px solid rgb(75,175,75);
}
.layout__visiblemenu {
  display: block!important;
  position: absolute;
  left: 0;
  z-index: 7;
  background-color: rgba(5,5,5,0.8);

}
.layout__menuitems li {
  display: block;
  align-items: center;
  margin: 0px 7px 0px 7px;
  font-size: 20px;
  font-weight: 600;
  position: relative;
  padding-top: 10px;
  padding-bottom: 10px;
}
.layout__page h1{
  font-size: 37px;
}
.layout__navBar  svg{
  font-size: 27px;
}
.layout__header a:hover{
  color: rgb(75,175,75);
}

}
/* remove browser default styling */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

.smallButton{
  width: 10%;
  height: 10%;

}

/* Setting font / Padding for most Headers */
h1, h2, h3, h4, h5, h6 {
  padding-top: .25em;
  padding-bottom: .25em;
  font-family: 'Open Sans', sans-serif;
  text-align: center;
}

/* Tweaks to H3 / H4 based on viewing */
h3{
  font-size: 24px;
  padding-bottom: 15px;
}
h4{
  padding-bottom: 15px;
}

/* Setting code writing to look more code like */
code{
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
}

/* Setting main body settings, inc bg / fotn color etc, using max width to narrow it down on PC's */
body {
  display: block;
  margin: auto;
  font-family: 'Roboto', sans-serif;
  color: rgb(235,235,235);
  max-width: 1040px;
  background-color: rgb(15,15,15);
  
}


/* Setting for most paragraph font */
p {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  display: block;
  margin-top: 1em;
  margin-bottom: 1em;
  white-space: pre-line;
}

/* List related settings */
ol {
  list-style: none;
  counter-reset: ListCounter;
}
li{
  padding-bottom: 10px;
  padding-top: 10px;
}
ol li {
  counter-increment: ListCounter;
}

ol li::before {
  content: counter(ListCounter) ". ";
  color: rgb(75,175,75);
  font-weight: bold;
}
.listPos{
  list-style-position: inside;
  border-left: 5px solid rgb(75,175,75);
  background-color: rgb(35,35,35);
  padding: 10px 20px;
  font-family: 'Open Sans', sans-serif;
}


/* Containers for various images */
.imageContain {
  max-width: 100%;
  height: auto;
  margin: auto;
  margin-top: 25px;
  margin-bottom: 25px;
  border: 2px solid rgb(235,235,235);
  display: block; 
}
.hover-index {
  max-width: 100%;
  height: auto;
  margin: auto;
  margin-top: .3em;
  margin-bottom: .3em;
  border: 2px solid rgb(235,235,235);
  display: block; 
}

/*  Hover Zoom over images  */
img.hover-index {
  transition: transform .25s;
  transform-origin: center center;
}
.hover-index:hover {
  opacity: 1;
  transform:scale(1.15);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* Settings for play button */
.playButton {
  max-width: 25vh;
  height: auto;
  margin: auto;
  margin-top: 25px;
  margin-bottom: 25px;
  display: block;
  cursor: pointer;
}

/* setting for the break line */
.imageBreak{
  padding-top: 5px;
  padding-bottom: 5px;
  max-width: 100%;
  height: 2.4vh;
  display: block;
  margin: auto;
}
/* setting for the break line */
.videoBreak {
  padding-top: 5px;
  padding-bottom: 5px;
  max-width: 100%;
  height: 7vh;
  display: block;
  margin: auto;
}


/* setting for main site container */
.siteContainer {
  display: block;
  margin: auto;
  padding-left: 1em;
  padding-right: 1em;
  background-color: rgb(15,15,15);

}

/* setting for header / footer */
.imageHeader {
  max-width: 100%;
  height: auto;
  display: block;
  margin: auto;
}
.imageFooter {
  max-width: 2.5em;
  padding-top: 5px;
  height: auto;
  display: block;
  margin: auto;
  background-color: rgb(75,175,75);
  
}
.footer {
  background-color: rgb(75,175,75);
}


/* Navbar settings */
.topnav {
  width: 100%;
  background-color: rgb(75,175,75);
  overflow: auto;
}

.topnav a {
  float: left;
  display: block;
  color: rgb(25,25,25);
  text-align: center;
  padding: .5em 20px;
  text-decoration: none;
  font-size: 1.2rem;
  font-family: 'Open Sans', sans-serif; 
  font-weight: bolder;  
}

.topnav a:hover {
  background-color: rgb(75,225,75);
}

.topnav a.active {
  background-color: rgb(75,175,75);
  color: #f2f2f2;
}

.topnav .icon {
  display: none;
  color: #f2f2f2;
}


/* Mobile navbar settings*/
@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {display: none;}
  .topnav a.icon {
    float: right;
    display: block;
  }
  p {
    font-size: 14px;
  }
}

@media screen and (max-width: 600px) {
  .topnav.responsive {position: relative;}
  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
}
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
}
}

/* Click to Zoom image settings */
.click-zoom input[type=checkbox] {
  display: none
}

.click-zoom img {
  transition: transform 0.25s ease;
  cursor: zoom-in
}

.click-zoom input[type=checkbox]:checked~img {
  transform: scale(2);
  cursor: zoom-out
}


